import React,{useState} from 'react'
import '../styles/about.css'

const About = () => {

//true = english, falsse = finnish
const [isEnglish, setIsEnglish] = useState(true)
const [content, setContent] = useState(`Hello there, I am a third year student thriving to be a Software developer! I am highly motivated on this area and I am always ready to learn something new even if it isn't related to the subject. I would say I am a pretty social person, working with people is my cup of tea. I know I might still have lots to learn but I am willing to put the effort in to it.`)
const translate = () => {
    if (isEnglish) {
        setIsEnglish(false)
        setContent('Hei, olen kolmannen vuoden datanomi opiskelija ja unelmani on olla Ohjelmisto kehittäjä! Olen todella motivoitunut toteuttamaan unelmaani ja olen aina valmis oppimaan uusia asioita vaikka ne eivät liittyisikään päämäärääni. Sanoisin, että olen aika sosiaalinen henkilö ja tykkään työskennellä muiden kanssa. Tiedän, että minulla on vielä paljon opittavaa mutta olen valmis työskentelemään sen eteen.')
    } else {
        setIsEnglish(true)
        setContent(`Hello there, I am a third year student thriving to be a Software developer! I am highly motivated on this area and I am always ready to learn something new even if it isn't related to the subject. I would say I am a pretty social person, working with people is my cup of tea. I know I might still have lots to learn but I am willing to put the effort in to it.`)
    }
    console.log(isEnglish)
}



return (
   <div className="about">
       <div className="about-me">
           <h3>About Me: </h3>
           <br/>
           <p className="content">{content}</p>
            <button className="btn" onClick={translate}>En/Fi</button>
            <a className="github" href="https://www.github.com/Aleksixaxa">GitHub</a>
       </div>
       <div className="line">
           <h2>Main skills:</h2>
       </div>
        <div className="skills">
            <div className="skill">
                <h3>HTML <span className="filled">★★★★</span><span className="not-filled">☆</span></h3>
                <p>2 years</p>
            </div>
            <div className="skill">
                <h3>CSS <span className="filled">★★★★</span><span className="not-filled">☆</span></h3>
                <p>2 years</p>
            </div>
            <div className="skill">
                <h3>Git / Github <span className="filled">★★★★</span><span className="not-filled">☆</span></h3>
                <p>2 years</p>
            </div>
            <div className="skill">
                <h3>Javascript <span className="filled">★★★</span><span className="not-filled">☆☆</span></h3>
                <p>1 year</p>
            </div>
            <div className="skill">
                <h3>React.js <span className="filled">★</span><span className="not-filled">☆☆☆☆</span></h3>
                <p>1 month</p>
            </div>
        </div>

        <div className="line">
            <h2>Things I want to learn/have tried:  </h2>
        </div>
        <div className="skills">
            <div className="skill">
                <h3>C#</h3>
            </div>
            <div className="skill">
                <h3>Python</h3>
            </div>
            <div className="skill">
                <h3>MongoDB</h3>
            </div>
            <div className="skill">
                <h3>Node.JS</h3>
            </div>
            <div className="skill">
                <h3>Unity/blender</h3>
            </div>
        </div>
    </div>



)
}


export default About