const Nav = () => {
return (
    <nav>
        <ul className="nav-links">
            <Link to="/">
                <li><span>H</span>ome</li>
            </Link>
            <Link to="projects">
                <li><span>P</span>rojects</li>
            </Link>
            <Link to="about">
                <li><span>A</span>bout</li>
            </Link>
         </ul>
    </nav>
)




}


export default Nav